}

#modal5 {
  background-image: url('../img/modal-bg-m.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  .form-head {
    width: 266px;
    height: 400px;
    margin: 15% auto;
    display: block;
    position: relative;
    background-color: rgba(0, 0, 0, .4);
    float: none;
    input:first-child {
      margin-top: 80px;
    }
    input {
      margin-top: 30px;
      margin-left: 15px;
      height: 45px;
      width: 235px;
      background-color: transparent;
      border: 1px solid rgba(255, 255, 255, 0.8);
      border-radius: 30px;
      color: #fff;
      &[type="text"] {
        padding-left: 20px;
        font-size: 14px;
        opacity: 1;
        font-family: $gothamLight;
        box-sizing: border-box;
      }
      &[type="email"] {
        padding-left: 20px;
        font-size: 14px;
        opacity: 1;
        font-family: $gothamLight;
        box-sizing: border-box;
      }
    }
    .form-head-button {
      margin: 30px auto;
      display: block;
      position: relative;
      width: 200px;
      cursor: pointer;
      text-align: center;
      line-height: 49px;
      height: 50px;
      border: none;
      outline: none;
      border-radius: 30px;
      background-color: #3dfbe5;
      color: #222;
      font-family: $gothamLight;
      font-size: 14px;
    }

  }

}

